import React from 'react'
import Slider from 'react-slick';

const Bannerslider = () => {
      const settings = {
    dots: false,
    infinite: true,
    speed: 1000,
    arrows: false, // Hide the previous and next arrows
    autoplay: true, // Enable autoplay
    slidesToShow: 1, // Initially, show 4 slides
    slidesToScroll: 1,
    responsive: [
      {
        breakpoint: 1024, // Adjust the settings for screens with width less than 1024px
        settings: {
          slidesToShow: 3,
        },
      },
      {
        breakpoint: 768, // Adjust the settings for screens with width less than 768px
        settings: {
          slidesToShow: 2,
        },
      },
      {
        breakpoint: 480, // Adjust the settings for screens with width less than 480px
        settings: {
          slidesToShow: 1,
        },
      },
    ],
  };
  return (
    <div className=''>
    <Slider {...settings}>
    <a href="#" className="d-block">
      <img className="w-100 __slide-img"  src="storage/app/public/banner/2023-06-13-64885cfee404f.png" alt />
    </a>
    <a href="#" className="d-block">
      <img className="w-100 __slide-img"  src="storage/app/public/banner/2023-06-13-64885ce9c7119.png" alt />
    </a>
    <a href="#" className="d-block">
      <img className="w-100 __slide-img"  src="storage/app/public/banner/2023-06-13-64885cdaaf0d6.png" alt />
    </a>

  </Slider>
    
    </div>
  )
}

export default Bannerslider