import React from 'react'
import Footer from '../Home_page/Footer'

const Contact = () => {
  return (
    <div>
    
  <div className="row">
    <div className="col-12" style={{marginTop: '10rem', position: 'fixed', zIndex: 9999}}>
      <div id="loading" style={{display: 'none'}}>
        <center>
          <img width={200} src="https://6valley.6amtech.com/storage/app/public/company/2022-04-23-62640d298e373.png" onerror="this.src='https://6valley.6amtech.com/public/assets/front-end/img/loader.gif'" />
        </center>
      </div>
    </div>
  </div>
  <div className="__inline-58">
    <div className="container rtl">
      <div className="row">
        <div className="col-md-12 sidebar_heading text-center mb-2">
          <h1 className="h3  mb-0 folot-left headerTitle">Contact Us</h1>
        </div>
      </div>
    </div>
    <div className="container rtl" style={{textAlign: 'left'}}>
      <div className="row no-gutters py-5">
        <div className="col-lg-6 iframe-full-height-wrap ">
          <img className="for-contac-image" src="https://6valley.6amtech.com/public/assets/front-end/png/contact.png" alt />
        </div>
        <div className="col-lg-6">
          <div className="card">
            <div className="card-body for-send-message">
              <h2 className="h4 mb-4 text-center font-semibold text-black">Send us a message</h2>
              <form action="https://6valley.6amtech.com/contact/store" method="POST" id="getResponse">
                <input type="hidden" name="_token" defaultValue="2wLJkfz6Mi59XBzVYYYW6OJPewymlh4keMvN2j60" /> <div className="row">
                  <div className="col-sm-6">
                    <div className="form-group">
                      <label>Your name</label>
                      <input className="form-control name" name="name" type="text" defaultValue placeholder="John Doe" required />
                    </div>
                  </div>
                  <div className="col-sm-6">
                    <div className="form-group">
                      <label htmlFor="cf-email">Email address</label>
                      <input className="form-control email" name="email" type="email" defaultValue placeholder="johndoe@email.com" required />
                    </div>
                  </div>
                  <div className="col-sm-6">
                    <div className="form-group">
                      <label htmlFor="cf-phone">Your phone</label>
                      <input className="form-control mobile_number" type="text" name="mobile_number" defaultValue placeholder="Contact number" required />
                    </div>
                  </div>
                  <div className="col-sm-6">
                    <div className="form-group">
                      <label htmlFor="cf-subject">Subject:</label>
                      <input className="form-control subject" type="text" name="subject" defaultValue placeholder="Short title" required />
                    </div>
                  </div>
                  <div className="col-md-12">
                    <div className="form-group">
                      <label htmlFor="cf-message">Message</label>
                      <textarea className="form-control message" name="message" rows={6} required defaultValue={""} />
                    </div>
                  </div>
                </div>
                <div id="recaptcha_element" className="w-100" data-type="image" />
                <br />
                <div className=" ">
                  <button className="btn btn--primary" type="submit">Send</button>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>

    </div>
  )
}

export default Contact