import React from 'react'
import Header from './Home_page/Header'
import { useNavigate } from 'react-router-dom'
import Footer from './Home_page/Footer'
import { useState } from 'react';
import OtpInput from 'react-otp-input';

const Login = () => {
  let navigate = useNavigate()
  const [otp, setOtp] = useState('');
  // style otp auto foucs
  const otpInputStyle = {
    width: "70%",
    height: "100px",
    marginBottom: "30px"
  };
  const inputStyle = {
    width: "70%",
    height: "50px",
    marginBottom: "10px",
    border: "1px solid gray",
    // backgroundColor: "#dff9c4",

    // Add any additional styles you want here
  };
  const inputProps = {
    type: "number",
  };
  let loginuser = (event) => {
    event.preventDefault()
    console.log("hello")
    let data = "hello"
    localStorage.setItem("loginstatus", data)
    navigate('/')
  }
  return (
    <div>

      <div>
        <span id="order_again_url" data-url="https://6valley.6amtech.com/cart/order-again" />
        <div className="modal fade" id="popup-modal">
          <div className="modal-dialog modal-dialog-centered" role="document">
            <div className="modal-content">
              <div className="modal-header border-0 p-0">
                <button type="button" className="close __close" data-dismiss="modal" aria-label="Close">
                  <span aria-hidden="true"></span>
                </button>
              </div>
              <div className="modal-body cursor-pointer __p-3px" onclick="location.href='https://codecanyon.net/item/6valley-multivendor-ecommerce-complete-ecommerce-mobile-app-web-and-admin-panel/31448597?s_rank=1'">
                <img className="d-block w-100" onerror="this.src='https://6valley.6amtech.com/public/assets/front-end/img/image-place-holder.png'" src="https://6valley.6amtech.com/storage/app/public/banner/2023-06-13-64885d57240cf.png" alt />
              </div>
            </div>
          </div>
        </div>
        <div className="modal-quick-view modal fade" id="quick-view" tabIndex={-1}>
          <div className="modal-dialog modal-xl">
            <div className="modal-content" id="quick-view-modal">
            </div>
          </div>
        </div>
        <style dangerouslySetInnerHTML={{ __html: "\n    .for-count-value {\n        color: #1455ac;\n    }\n\n    .count-value {\n        color: #1455ac;\n    }\n\n    @media (min-width: 768px) {\n        .navbar-stuck-menu {\n            background-color: #1455ac;\n        }\n\n    }\n\n    @media (max-width: 767px) {\n        .search_button .input-group-text i {\n            color: #1455ac                              !important;\n        }\n        .navbar-expand-md .dropdown-menu > .dropdown > .dropdown-toggle {\n            padding- right: 1.95rem;\n        }\n\n        .mega-nav1 {\n            color: #1455ac                              !important;\n        }\n\n        .mega-nav1 .nav-link {\n            color: #1455ac                              !important;\n        }\n    }\n\n    @media (max-width: 471px) {\n        .mega-nav1 {\n            color: #1455ac                              !important;\n        }\n        .mega-nav1 .nav-link {\n            color: #1455ac !important;\n        }\n    }\n    /**/\n" }} />
        <Header />
        <span id="authentication-status" data-auth="false" />

        <div className="container py-4 py-lg-5 my-4" style={{ textAlign: 'left' }}>
          <div className="login-card">
            <div className="mx-auto __max-w-360">
              <h2 className="text-center h4 mb-4 font-bold text-capitalize fs-18-mobile">Login</h2>
              <form className="needs-validation mt-2"  >
                <input type="hidden" /> <div className="form-group">
                  <label className="form-label font-semibold">Phone</label>
                  <input className="form-control" type="text" style={{ textAlign: 'left' }} defaultValue placeholder="Enter email address or phone number" />
                  <div className="invalid-feedback">Please provide valid email or phone number .</div>
                </div>
                <div className="form-group">

                  <div className="password-toggle rtl">

                    <OtpInput
                      value={otp}
                      onChange={setOtp}
                      numInputs={6}
                      renderSeparator={<span>-</span>}
                      inputStyle={inputStyle}
                      style={otpInputStyle}
                      renderInput={(props) => <input {...props} />}
                      inputProps={inputProps}
                    />
                  </div>
                </div>
                {/* <div className="form-group d-flex flex-wrap justify-content-between">
         
            <a className="font-size-sm text-primary text-underline">
              Forgot password?
            </a>
          </div>
          */}
              </form>
              <button className="btn btn--primary btn-block btn-shadow" type="submit" onClick={loginuser}>Log in</button>

            </div>
          </div>
        </div>



        <Footer />



      </div>


    </div>
  )
}

export default Login