import React, { useEffect } from 'react'
import { useNavigate } from 'react-router-dom'

const Protact = (props) => {
    let navigate = useNavigate()
    localStorage.setItem("p_status", true)
    let { ComponentName } = props
    // console.log(ComponentName)
    let data = true
    useEffect(() => {
        data == true ? navigate("/login") : navigate("/")
    }, [data])


    return (
        <> {data == true ? (navigate("/login")) : (<ComponentName />)}   </>
    )
}

export default Protact